import { auth } from "../../firebase";
import { toast } from "react-toastify";
import { useSelector } from "react-redux";
import React, { useEffect, useState } from "react";

// icons
import { TbLogin } from "react-icons/tb";
import SEO from "../../components/SEO";

const Register = ({ history }) => {
  const [email, setEmail] = useState("");

  const { user } = useSelector((state) => state);

  useEffect(() => {
    if (user && user.token) history.push("/");
  }, [user]);

  const handleSubmit = async (e) => {
    e.preventDefault();

    // docs
    const config = {
      url: "http://localhost:3000/register/complete",
      handleCodeInApp: true,
    };

    await auth.sendSignInLinkToEmail(email, config);

    toast.success(
      `Email is sent to ${email}, tap to complete your registeration`
    );

    // save user to local storage
    window.localStorage.setItem("emailForRegisteration", email);

    // clear state
    setEmail("");
  };

  return (
    <>
    <SEO title={"Register"} />
    <section className="text-gray-600 body-font relative">
      <div className="container px-5 py-10 mx-auto">
        <div className="flex flex-col items-center w-full mb-12">
          <h1 class="sm:text-3xl text-2xl  inline-flex  items-center font-medium title-font mb-4 text-gray-700">
            Register <TbLogin className="mx-1" color="blue" />{" "}
          </h1>
        </div>
        <form>
          <div className="lg:w-1/2 md:w-2/3 mx-auto">
            <div className="flex flex-wrap -m-2">
              <div className="p-2 w-full">
                <div className="relative">
                  <label
                    for="email"
                    className="leading-7 text-sm text-gray-600"
                  >
                    Email
                  </label>
                  <input
                    type="email"
                    id="email"
                    name="email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    placeholder="Your email"
                    autoFocus
                    className="w-full bg-gray-100 bg-opacity-50 rounded border border-gray-300 focus:border-indigo-500 focus:bg-white focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
                  />
                </div>
              </div>

              <div className=" p-2 w-full" onClick={handleSubmit}>
                <button
                  disabled={!email}
                  className="flex mx-auto text-white   bg-indigo-500 border-0 py-2 px-8 focus:outline-none hover:bg-indigo-600 rounded text-lg"
                >
                  Register
                </button>
              </div>
            </div>
          </div>
        </form>
      </div>
    </section>
    </>
  );
};

export default Register;
